@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Merienda&display=swap);
@import url(https://fonts.googleapis.com/css?family=Saira+Stencil+One&display=swap);
.searching_form{
    width: 92vw;
    margin: 0 auto;
    padding-top: 35px;

    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.searching_form #search{
  width: 89vw;
}
.pages_list{
    width: 40vw;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 10px auto;
}

.pages_list .button{
    width: 60px;
}

.pages_list .section{
    width: 25px;
}

.pages_list .button, .pages_list .section{
    background: #3f51b5;
    text-align: center;
    margin: 3px;
    cursor: pointer;
    color: #ffffff;

    font-family: 'Merienda';

    border-radius: 5px;
}

.pages_list .button:hover, .pages_list .section:hover{
    color: rgb(255,0,0);
}

@media screen and (max-width: 780px)
{
    .pages_list{
        width: 80vw;
    }
}
.filter_form {
    display: none;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;

    width: 100vw;
    height: 100vh;
   
    background: rgba(0,0,0,0.6);
}

.filter{
    width: 30vw;
    z-index: 200;
    padding-bottom: 5px;
    
    background: #ffffff;
}

.filter .header{
    height: 65px;
    padding: 5px 0px;
    
    text-align: center;

    color: #ffffff;
    background: #000000;
}

.filter .header h1, .filter .header h4{
    margin: 0px;
} 

.filter .check{
    padding:  auto;
    margin: 10px 0;
}

.filter .check .check_min{
    width: 90%;
    margin: 0 auto;
}

.filter .check .check_min div{
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
}

.filter .check .types{
    text-align: center;
}

.filter .check .types h3{
    margin-bottom: 5px;
}

.filter .check .types .checkBoxes{
    display: flex;
    flex-wrap: wrap;

    font-size: 16px;
}

.filter .check .types .checkBoxes div{
    width: 20%;
    margin-left: 10px;

    display: flex;
    justify-content: flex-start;
}

.filter .check .clear{
    margin-top: 10px;
    cursor: pointer;
    font-family: 'Merienda';
}

.filter .check .clear:hover{
    color: rgb(255,0,0);
}
@media screen and (max-width: 320px)
{
    .filter{
        min-width: 90vw;
    }
}

@media screen and (max-width: 360px)
{
    .filter .check .types .checkBoxes div{
        margin-left: 20px;
    }
}
@media screen and (max-width: 720px)
{
    .filter{
        width: 80vw;
    }
}

@media screen and (max-width: 780px)
{
    .filter{
        min-width: 50vw;
    }
}

@media screen and (max-width: 1024px)
{
    .filter{
        min-width: 40vw;
    }
}
.footer{
  height: 70px;

  display:flex;
  flex-direction: column;
  justify-content: center;

  font-family: 'Saira Stencil One', cursive;
  font-size: 16px;
  text-align: center;

  background-color: #3f51b5;
  color: white;
}
.menu{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

.head_menu{
    height: 65px;
    width: 250px;

    font-size: 20px;
    font-weight: bold;
    text-align: center;
  
    color: white;
    background-color: black;
}

.head_text{
    text-decoration: none;
    color: rgb(255,255,255);
}

.head_text:active{
    font-size: 90%;
}
  
.menu_section{
    height: 30px;
    margin-left: 30px;
    padding-top: 15px;
}
  
.menu_section a{
    font-size: 16px;
    text-decoration: none;

    color: #3C3838;
}
  
.menu_section p:hover, .menu_section a:hover{
    color: black;
}
.card{
    height: 140px;
    width: 140px;

    margin-top: 30px;
    margin-right: 30px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    background-image: url(/Pokedex/static/media/card_background.36022bff.jpg);
}

.img_of_card{
    height: 110px;

    display:flex;
    justify-content: center;
}

.img_of_card img{
    width: 110px;
}

.text_of_card{ 
    text-align: center;
    text-decoration: none;
    background-color: rgba(0,0,0,0.4);
}

.text_of_card .head_card{
    font-style: italic;
    font-weight: bold;
    font-size: 14px;
    
    color: #ffffff;
}
.background_card{
    display: flex;
    justify-content: center;
    
    align-items: center;
    min-height: 90vh;

    background-color: #f2e9e9;
}

.background_card .back_icon{
    position: absolute;
    top: 95px;
    right: 97%;

    color: #000000;
}

.background_card .back_icon:hover{
    color: rgb(255,0,0);
}

.info_card{
    width: 50vw;
    margin-top: 50px;
    padding-bottom: 25px;

    background-color: rgb(255,255,255);
}

.info_card .head{
    height: 65px;
    padding: 0 5px;

    display: flex;
    justify-content: space-between;
    align-items: center;
    
    font-size: 24px;
    font-weight: bold;
    font-style: italic;

    background-color: rgb(255,0,0);
    color: rgb(255,255,255);
}

.info_card .types{
    display: flex;
    justify-content: center;
}

.info_card .types .type{
    margin: 10px;
    padding: 5px 10px;

    font-weight: bold;
    font-style: italic;

    border-radius: 8px;

    color: rgb(255,255,255);
}

.info_card .types .one{
    background-color: #aa137a;
}

.info_card .types .two{
    background-color: #0beaf9
}

.info_card .main_info{
    display: flex;
    justify-content: space-around;

    margin: 0px;
}

.info_card .main_info .image{   
    display: flex;
    align-items: center; 
}

.info_card .main_info .image img{
    width: 12vw;
}

.info_card .main_info .skils{
    width: 20vw;
    display: flex;
    flex-direction: column;
    justify-content: center;

    font-size: 16px;
}

.info_card .main_info .skils div{
    display: flex;
    justify-content: space-between;
}

.info_card .main_info .count{
    width: 75%;
    margin-bottom: 3px;
    
    text-align: right;
    
    background-color: #d4d4d4;
}

.info_card .profile{
    margin: 0 1%; 
}

.info_card h4{
    height: 25px;    

    font-style: italic;
    text-align: center;

    background-color: #d90707;
    color: rgb(255,255,255);
}

.info_card .profile .info_profile{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    font-size: 16px;
}

.info_card .profile .info_profile div{
    width: 45%;
    margin-bottom: 3px;

    display: flex;
    justify-content: space-between;
}

.info_card .profile .info_profile .prop{
    width: 40%;
    font-weight:  bold;
}

.info_card .profile .info_profile .c_profile{
    width: 50%;
}

@media screen and (max-width: 480px)
{
    .background_card .back_icon{
        top: 90px;
        right: 85%;
    }

    .background_card .back_icon:hover{
        color: #ffffff;
    }

    .info_card{
        margin-top: 70px;
        margin-bottom: 10px;
    }
    .info_card .main_info{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .info_card .main_info .image img{
        min-width: 50vw;
    }
 
    .info_card .main_info .skils{
        min-width: 90%;
     }

    .info_card .profile .info_profile{
        justify-content: center;
        align-items: center;
        flex-direction: column;
    
        font-size: 14px;
    }

    .info_card .profile .info_profile div{
        width: 90%;
    }
}

@media screen and (max-width: 1080px)
{
    .info_card{
        width: 90vw;
    }

    .info_card .main_info .image img{
        width: 20vw; 
    }

    .info_card .main_info .skils{
        width: 40vw;
    }
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app{
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  min-height: 100vh;
}

.start{
  margin-top: 65px;
  min-height: 85vh;
}

.body{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.result_of_search{
  margin-top: 10px;
  margin-bottom: 10px;
}

.result_of_search .props{
  text-align: center;
}

